import React from 'react';
import '../styles/Friend.scss'
import '../styles/Photo.scss'

function Photo(props) {
    return (
        <div className="photo" style={{backgroundImage: `url(${props.url})`}}>
            <img src="icons/delete.svg" alt="delete svg" className="delete-btn"></img>
        </div>
    )
}

export default Photo;
