import React from 'react';
import Photo from "./Photo";
import '../styles/Media.scss'
import {observer} from "mobx-react-lite";

const Media = observer(props => {

    const photos = [
        {
            url: "https://img.freepik.com/free-photo/smiling-man-holding-smartphone_107420-20811.jpg?size=626&ext=jpg&ga=GA1.2.2031557682.1626652800",
        },
        {
            url: "https://media-exp1.licdn.com/dms/image/C4E03AQHPHGZFJKi_qg/profile-displayphoto-shrink_200_200/0/1517603271195?e=1629936000&v=beta&t=ZNs9IjvthhRpGBaMIxGFzCcA0lO7keE5J7WGIlcFxP4",
        },
        {
            url: "https://pbs.twimg.com/profile_images/1039624216746704896/cM4YHvUf_400x400.jpg",
        },
        {
            url: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTZL4AJkTHRbdhesxckFLeqYnQT5jJL6QiE9A&usqp=CAU",
        },
        {
            url: "https://images.unsplash.com/photo-1544465544-1b71aee9dfa3?ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTF8fHBob3RvfGVufDB8fDB8fA%3D%3D&ixlib=rb-1.2.1&w=1000&q=80",
        },
        {
            url: "https://api.time.com/wp-content/uploads/2019/08/better-smartphone-photos.jpg",
        },
        {
            url: "https://resources.stuff.co.nz/content/dam/images/4/y/p/u/2/9/image.related.StuffLandscapeSixteenByNine.710x400.4ypty9.png/1607542726485.jpg?format=pjpg&optimize=medium",
        },
        {
            url: "https://www.photo-montier.org/wp-content/themes/ktheme-v2.1-Montier/images/slider-homepage/2020/640x628-dean-gill/dean.jpg",
        },
        {
            url: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQYAHZ0lpBtlP8lXebTIEtnA5-gDxLrPaL-CA&usqp=CAU",
        },
    ]

    return (
        <section className="media">
            <p className="section-title">My Media</p>
            <div className="photos">
                {
                    photos.map((val) => {
                        return <Photo url={val.url} key={val.url}/>
                    })
                }
            </div>
            <div className="buttons">
                <button className="main-btn violet">Delete</button>
                <label className={"main-btn white"} htmlFor="upload-photo">Add</label>
                <input type="file" name="photo" id="upload-photo" accept=".jpg, .jpeg, .png"/>
            </div>
        </section>
    )
})

export default Media;
