import React from 'react';
import Friend from "./Friend";
import '../styles/Friends.scss'
import {observer, Observer} from "mobx-react-lite";

const Friends = (props => {

    const friends = [
        {
            name: "Corey George",
            job: "Developer",
            image: "https://img.freepik.com/free-photo/smiling-man-holding-smartphone_107420-20811.jpg?size=626&ext=jpg&ga=GA1.2.2031557682.1626652800",
        },
        {
            name: "Thomas Wilkinson",
            job: "Developer",
            image: "https://media-exp1.licdn.com/dms/image/C4E03AQHPHGZFJKi_qg/profile-displayphoto-shrink_200_200/0/1517603271195?e=1629936000&v=beta&t=ZNs9IjvthhRpGBaMIxGFzCcA0lO7keE5J7WGIlcFxP4",
        },
        {
            name: "Charles Meyers",
            job: "Developer",
            image: "https://pbs.twimg.com/profile_images/1039624216746704896/cM4YHvUf_400x400.jpg",
        },
        {
            name: "Tiana Korsgaard",
            job: "Developer",
            image: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTZL4AJkTHRbdhesxckFLeqYnQT5jJL6QiE9A&usqp=CAU",
        },
    ]

    return (
            <section className="friends">
                <p className="section-title">Friends</p>
                <div className="friends-list">
                    {
                        friends.map((val, index) => {
                            return (<Friend name={val.name} job={val.job} image={val.image} hasUnderline={index < friends.length - 1} key={val.name}/>)
                        })
                    }
                </div>
                <div href="##" className="add-friend-btn">
                    <p>add friend</p>
                    <img src="./icons/add.svg" alt="add icon"></img>
                </div>
            </section>
    )
})

export default Friends;
