import {action, makeObservable, observable} from "mobx";

class AppStore {
    darkMode = false;
    type = 'Black';
    constructor(darkMode) {
        makeObservable(this, {
            darkMode: observable,
            type: observable,
            toggleMode: action,
            changeType: action
        })
    }
    toggleMode() {
        if (this.darkMode == true) {
           this.darkMode = false;
           return;
        }
        this.darkMode = true;
    }
    changeType(value) {
        this.type = value;
        console.log(this.type)
    }
}
export default new AppStore();
