import React from 'react';
import '../styles/Header.scss'
import {observer} from "mobx-react-lite";
import DarkModeToggle from "react-dark-mode-toggle";

const Header = observer(props => {
    return (
        <header className="header">
            <div className="back-btn">
                <img src={`./icons/back${props.appStore.darkMode == true ? '-white' : ''}.svg`} alt="back button"></img>
            </div>
            <p className="title">Profile</p>
            <DarkModeToggle
                onChange={() => {props.appStore.toggleMode()}}
                checked={props.appStore.darkMode}
                size={80}
            />
            <div className="menu-btn">
                <img src={`./icons/menu${props.appStore.darkMode == true ? '-white' : ''}.svg`} alt="menu button"></img>
            </div>
        </header>
    )
})

export default Header;
