import React, {useState} from "react";
import Type from "./Type";
import {observer} from "mobx-react-lite";

const TypesHolder = observer(props =>{
    const types = ["Black","Bold","Medium","Regular","Light","Italic"]

    return (
    <div className="types-holder">
        {types.map((val) => {
            return <Type value={val} isSelected={props.appStore.type === val} key={val} darkMode={props.appStore.darkMode} appStore={props.appStore}/>
        })}
    </div>
    );
})

export default TypesHolder;
