import React, {useState} from "react";
import {observer} from "mobx-react-lite";

const violet500 = '#6200EE';
const selectedTypeBg = '#F2E7FE'
const selectedTypeBorder = '#DBB2FF';

const Type = observer(props => {
    return(<div className={`type ${props.darkMode == true ? 'dark-mode' : ''}`}
                onClick={() => {props.appStore.changeType(props.value)}}
                style={{color: props.isSelected ? violet500 : '', backgroundColor: props.isSelected ? selectedTypeBg : '', border: props.isSelected ? '1px solid '+selectedTypeBorder : ''}}>
        {props.value}
    </div>)
})

export default Type;
