import React from 'react';
import '../styles/Friend.scss'
function Friend(props) {
    return (
        <div className="friend-wrapper">
            <div className="friend">
                <div href="##" className="profile-img" style={{
                    backgroundImage: `url(${props.image})`
                }}></div>
                <div className="about">
                    <p className="title">{props.name}</p>
                    <p className="subtitle">{props.job}</p>
                </div>
                <img className="rm-friend-btn" src="./icons/close-red.svg" alt="remove frien button"></img>
            </div>
            {props.hasUnderline ? <hr></hr> : true}
        </div>
    )
}

export default Friend;
